/*  ALERTS  */

h2.system-alerts {
  margin: 20px 0;
  font-size: 35px;
}
ul.system-alerts, ul.system-overall, ul.station-alerts-am, ul.station-alerts-pm {
  clear: both;
}

.station-alerts-am, .station-alerts-pm, .system-alerts {
  clear: both;
  margin-bottom: 20px;
  // border: 1px solid #ccc;
  // padding: 20px;
  // border-radius: 2px;
}
.station-alerts-am h2, .station-alerts-pm h2, .system-alerts h2 {
  margin-bottom: 20px;
}

li.alertbox {
  padding: 20px;
  margin-top: 5px;
  position: relative;
}

.alertbox b {
  font-size: 30px;
}

.alertbox p {
  margin-top: 12px;
  font-size: 20px;
}

.alertbox .icon.close-icon {
  position: absolute;
  top: 3px;
  right: 10px;
}

.alertbox .sev-score {
  position: relative;
  top: 5px;
  font-size: 16px;
  left: 3px;
}