/* CSS browser reset */
* {
  margin: 0;
  padding: 0;
}

/* OVERALL */
html {
  visibility: hidden;
  opacity: 0;
}

body {
  font-family: Helvetica, Arial, sans-serif, sans-serif;
  margin: 0;
  -webkit-backface-visibility: hidden;
}

.fixed {
  position: fixed;
}

.hidden {
  display: none;
}

img.loading-gif.fixed {
  top: 30%;
  z-index: 100;
  left: 45%;
}

.content-wrapper {
  width: 1000px;
  margin: 0 auto;
  margin-top: 70px;
}

h2 {
  font-size: 1.8em;
  display: inline-block;
}
